import axios from 'axios';
import imageCard from '../templates/image-card.hbs';
import refs from './refs';

export default {
  apiKey: '16252810-97a93e8d6856e870fd6ba2eb3',
  searchQuery: '',
  page: 1,
  fetchArticles(value) {
    this.searchQuery = value;

    axios.defaults.baseURL =
      `https://pixabay.com/api/?image_type=photo` +
      `&orientation=horizontal&q=${this.searchQuery}&page=${this.page}` +
      `&per_page=12&key=${this.apiKey}`;

    axios.get().then(({ data: { hits } }) => {
      const markup = showResults(hits);
      console.log(markup);
      refs.galleryRef.insertAdjacentHTML('beforeend', markup);
    });
  },
};

function showResults(data) {
  return imageCard(data);
}
